export default {

	name: 'TimeEntries',

	props: {},

	data() {
		return {};
	},

	methods: {
		deletePlan(index){
			this.$store.dispatch('decTotalTime',this.plans[index].totalTime);
			this.$store.dispatch('deletePlan',index)
		}

	},
	
	computed: {		
		plans(){
			console.log(this.$store.state)
			return this.$store.state.list;
		}
	},

//过滤器
filters: {},

mounted() { this.$nextTick(() => { }) },

//Vue 实例销毁后调用
destroyed() { },

components: {},

watch: {}

};


